var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-sm-12"},[_c('h1',[_vm._v("The World of Altasoror")]),_c('b-tabs',[_c('b-tab',{attrs:{"title":"Intro"}},[_c('div',[_c('p',[_vm._v("\n          Welcome to the world of Altasoror, a world created for the Between Worlds campaign.\n        ")]),_c('p',[_vm._v("\n          It all began with the prophet. Thousands of years ago, the prophet Sorore (Sore - Or) walked upon the planet and taught the world and its people all there was to know. The prophet taught the world of the one true god, the great creator, who guides us all towards prosperity. The prophet traveled to all parts of the world and spread the Creator’s message. The prophet spoke of peace, of rules, of cooperation, of humanity itself. The prophet created the “Tenants of Order” which all should follow. The prophet also knew how to build society, from its government to its cities, to its farmland. The Creator gifted the prophet will all knowledge. Once the world was taught, the prophet left leaving only one thing behind.\n        ")]),_c('p',[_vm._v("\n          In the city of Sornal, in the kingdom of Sesta, where the prophet was born. The prophet left behind a stone tablet, inscribed with dire warning of doom, and the world's potential salvation. A hero with silver hair will stop the world from falling to this darkness. Unfortunately, the exact wording of the warning is unknown, as the Sesta have betrayed the prophet’s will, and guard the tablet from those who would read it.\n        ")]),_c('p',[_vm._v("\n          Since then, the world has become more divided in its interpretation of the prophets tenants. Those born with silver hair are raised as potential saviors. Trained in sword and diplomacy, to protect against all possible threats which may destroy the world. But all have been false saviors, having died in their journey, or turning to corruption. The world continues waiting for the true hero, and the doom that comes with.\n        ")]),_c('p',[_vm._v("\n          Those who have tried to expand upon the world’s knowledge are met with a horrid fate. Those to delve into magic are consumed by it, either killed or driven mad. Those who attempt to advance technology meet with similar results. The world has not evolved much since the prophet left. Innovation is stagnant, witchcraft is forbidden in all parts of the world, and inventors are despised, or worse.\n        ")]),_c('p',[_vm._v("\n          But this world is mostly peaceful. No great evils threaten the world, and mythical beasts are just that, myth. Political discourse is mostly positive throughout the world, save for the barbaric Sesta, and the lawless lands of Astipan. It is exactly as the prophet foretold. Which makes the world even more cautious about the prophecy of doom that the Sesta are hiding.\n        ")])])]),_c('b-tab',{attrs:{"title":"Atlas"}},[_c('div',[_c('h3',[_vm._v("Map of Altasoror")]),_c('img',{attrs:{"src":"https://elthelas-images.herokuapp.com/maps/altasoror-map.jpg","alt":"map of altasoror"}}),_c('h3',[_vm._v("Kingdom Descriptions")]),_c('h3',[_vm._v("Estar")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Labour, workers"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Monarchy"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Jalen Raldrud"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Ochar, Astipan, Malthein"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 4 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sororism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Estapor"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorore’s Blade. A sword that was supposedly made for Sorore, who supposedly carried it, but never used it."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Estar is a very traditional kingdom of a King, Queen, Lords, and knights. They most closely follow the tenants of order. Estar attempts to be the enforcers of the world, when you need help or a hero, you go to Estar. They share a border with the barbaric Sesta, and are often in conflicts with them.\n        "),_c('h3',[_vm._v("Malthein")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Salt, Minerals"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Elder council"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Sasha Lao"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Estar, Frugua, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 300,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soroism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Titan’s Heart"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorore’s Podium. A unique geological location in the mountains that create a massive echo effect to give grand speeches to the open area below."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" The people of Malthein lead a more reclusive lifestyle. The live among and even inside the mountains of Malthein. The follow Soroism but only mildly, and place more emphasis on wisdom and decision making. They travel the mountains regularly on spiritual quests of enlightenment.\n        "),_c('h3',[_vm._v("Sesta")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Slaves"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Leader completes a trail set by the previous leader"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Currently Undecided"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Astipan, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Frugua, Estar, Chechniar, Malthein"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 1 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sornal (Named after the city)"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Sornal, The Silent City"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorore’s tablet, which contains the prophecy of doom. Hidden deep within the Silent City. Forbidden to all except the Sesta’s leader."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" The Sesta are a strong and proud people. Valuing their ancestors wisdom above all. After the time of Sorore, their ancestors decided that Sorore was a prophet of doom and destruction. They rejected the beliefs of Sororism and creating their own “religion” called Sornal. It rejects all the tenants and does as much the opposite to oppose Sorore as possible. They enslave others, have destroyed most of Astipan, wage war with Estar, and take from Ochar."),_c('br'),_c('h3',[_vm._v("Frugua")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Animal and Fur"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Democracy"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Zoldim Chemtil"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Malchein, Chechniar, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 800,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soric"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Ukalale"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Frugua is basically a desert, as the mountain ranges of Malthein block most rains to the area. The people that live here are a close knit community, focusing a lot on family and cooperation. Though they follow the teachings of soric, they aren’t especially religious. They live in moving tent colonies to avoid the most extreme weather of the lands. Most travelers to Frugua tend to stay, as the feeling of community and cooperation can’t truly be found elsewhere in the world. Trade is scarce, as they have little to give, and trips out into the desert are harsh for the unprepared. People of Frugua tend to be shorter, and more slender than other places in the world.\n        "),_c('h3',[_vm._v("Astipan")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 200,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" The Blood Tree. A one of a kind tree of unknown origin. The Sesta waged war with Astipan to destroy the tree, believing it to be from Soror, once they reached it they decided against destroying it."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Astipan has fallen to disarray since being destroyed by Sesta, there is no government, no set cities left. Bandits, Wizards, Inventors flee to Astipan to avoid oversight. The people mostly fled to Estar as well as Ochal and Checkniar.\n        "),_c('h3',[_vm._v("Ochal")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Food, grains, livestock"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Many local elected leaders manage individual townships"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Several"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" All"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 350,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sororism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Dragon’s rest. A large hill that is out of place with the rest of the landscape."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Ochal is the main source of farmland throughout the region. The land has spotted small towns of trade centers, but they are few and far between. There is no set leader, other than minor town leaders. As the central country, all other countries must go here for extra food for the winter. Even though it has little in the way of defenses, it makes offerings to all other countries in order to keep the peace.\n        "),_c('h3',[_vm._v("Chechniar")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Luxury goods"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Theology"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Sorom Enrico Gritti"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Frugua, Ochar,"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 1.5 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soric"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Venier"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Mantle of the Prophet. A scarf that the Sorom wears, it was supposedly worn by the prophet."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Chechniar is the kingdom of wealth and prosperity. A land rich in money and religion. It is the central land for the religion of Soric. A far more “catholic” style religion, featuring a pope-like figure In the Sorom, who claims to be the voice of the prophet. In reality, this church is mostly corrupted by greed and wealth. The practices that made the kingdom rich have been exploited to make the rich richer. And having a religious figure as their leader, who is then appointed by a council of other religious figures, very little looks like it will change for the better.\n      ")])]),_c('b-tab',{attrs:{"title":"Timelines"}},[_c('h3',[_vm._v("Available Time Portals")]),_c('h