ed in AS – After Soror.")]),_c('p',[_vm._v("Sorore – with an E, referenced the prophet directly. For all other uses, the E is dropped.")]),_c('p',[_vm._v("Potential Saviors – Those born with silver hair, are expected to visit all the counties of the world during their journey. If they are born in a place without the structure to train them, Malthein will always take them in. The silver hair of savior is quite distinct, and no hair dye or magic has been able to replicate it’s appearance. Anyone who has seen a true silver haired savoir would easily be able to tell the difference.")]),_c('h3',[_vm._v("Religions")]),_c('p',[_vm._v("There are only 3 religions in the world, described below.")]),_c('p',[_vm._v("Sororism – The most common religion in Altasoror. It is a general worship of the creator and following the prophets will. There are no required church meetings, tithes or rituals.")]),_c('p',[_vm._v("Soric -  A far more organized religion that Sororism. The soric church governs over Chechniar, and all it’s inhabitants are required to pay a monthly tithe to the church. There is a very set organizational hierarchy, with the Sorom being the religious and governmental leader of the country.")]),_c('p',[_vm._v("Sornal – Only followed by the Sesta. They believed the teachings of the prophet would lead to the doom of the world. As such, they do everything in their power to disregard and work against the prophet’s will and those that would follow them.")])]),_c('b-tab',{attrs:{"title":"NPCs"}},[_c('h2',[_vm._v("Allies")]),_c('div',[_c('h4',[_vm._v("Rutherford Hayes")]),_c('p',[_vm._v("\n          Sanctuary - has taken to working through the library in the Sanctuary to learn about smithing enchanted materials.\n        ")]),_c('p',[_vm._v("\n          Altasoror - Year 1363 Day 122 - Best smith in Titan's heart. Interested in Rito smithing techniques and meteoric metal. Forged Hanzo's meteor armor and then chose to accompany Elise and the party on their quest between worlds.\